@import '../../styles/settings.scss';

.booking {
  max-width: 100%;
  height: 37.5rem;
  display: flex;
  justify-content: space-between;
  padding: 0 11%;
  background: url(../../images/booking/hero-bg-desktop.jpg);
}

.booking__img{
  background: url(../../images/patterns/pattern-curve-bottom-right.svg) no-repeat;
  background-position: bottom left;
  background-position-x: -1.5%;
  height: 20rem;
}

.pattern__lines {
  position: relative;
  top: 51%;
  left: 45%;
  cursor: default;
}

.button__mobile {
  display: none;
}


// --------------- MEDIA ---------------

@media screen and (max-width: 480px) {

  .booking {
    background: url(../../images/booking/hero-bg-mobile@2x.jpg) no-repeat;
    background-position-x: center;
    background-size: cover;
    width: 100%;
    max-height: 37.5rem;
    flex-direction: column;
    padding: 0 5%;
    justify-content: start;
  }

  .pattern__lines {
    display: none;
  }

  .booking__img {
    background: none;
    height: 41.85rem;
  }

  .button__mobile {
    display: flex;
    justify-content: center;
    margin-top: -0.6rem;
    cursor: default;

    p {
      text-transform: uppercase;
      background: transparent;
      border: 1px solid $white;
      color: $white;
      padding: 1.5rem 3.5rem;
      word-spacing: 3px;
      letter-spacing: 2px;
      font-family: $font-text;
      font-size: 1rem;
    }
  }
}

@media screen and (min-width: 481px) and (max-width: 1024px) {

  .booking {
    background: url(../../images/booking/hero-bg-tablet@2x.jpg) no-repeat;
    background-position-x: center;
    background-size: cover;
    width: 100%;
    max-height: 37.5rem;
    flex-direction: column;
    padding: 0 5%;
    justify-content: start;
  }

  .pattern__lines {
    display: none;
  }

  .booking__img {
    background: none;
    height: 41.65rem;
  }
}
